.App {
  text-align: center;
  font-size: clamp(0.45rem, 1.2vw, 1rem);
}

.result {
  font-size: 2em;
}

form {
  margin: 1em;
}

input {
  font-size: 1.3em;
  padding: 0.7em;
}

button {
  font-size: 1.1em;
  padding: 1em;
  cursor: pointer;
  background-color: #1473e6;
  border-color: #1473e6;
  color: #fff;
  margin: 0 0.5em;
}

button:hover {
  background-color: #0d66d0;
  border-color: #0d66d0;
}
